<script>
import qs from "qs";
import datepicker from "@/components/DatePicker";
// import 'bootstrap/dist/css/bootstrap.css'
// import 'bootstrap-vue/dist/bootstrap-vue.css'

export default {
  components: { datepicker },
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //
      NursingassessmentItems: {},
      obstacleArr: [],
    };
  },

  mounted() {
    this.setAllData();
    this.getNursingassessment();
  },

  methods: {
    filtermarry() {
      if (this.list != undefined) {
        var item = this.list;
        var rs = "";
        if (item.marry1 == "true") {
          rs += ";";
        }
        if (item.marry2 == "true") {
          rs += ";";
        }
        if (item.marry3 == "true") {
          rs += ";";
        }
        if (item.marry4 == "true") {
          rs += ";";
        }
        if (item.marry5 == "true") {
          rs += ";";
        }
        if (item.marry6 == "true") {
          rs += ";";
        }
        if (item.marry7.length > 0) {
          rs += item.marry7;
        }
        return rs;
      }
    },
    filterlanguage() {
      if (this.list != undefined) {
        var item = this.list;
        var rs = "";
        if (item.language1 == "true") {
          rs += ";";
        }
        if (item.language2 == "true") {
          rs += ";";
        }
        if (item.language3 == "true") {
          rs += ";";
        }
        if (item.language4 == "true") {
          rs += ";";
        }
        if (item.language5 == "true") {
          rs += ";";
        }
        if (item.language6 == "true") {
          rs += ";";
        }
        if (item.language7 == "true") {
          rs += ";";
        }
        if (item.language8.length > 0) {
          rs += item.language8;
        }
        return rs;
      }
    },
    setAllData() {
      this.cData = JSON.parse(sessionStorage.getItem("cData"));
      this.pData = JSON.parse(sessionStorage.getItem("pData"));
      // this.list = this.uData = JSON.parse(sessionStorage.getItem("uData"));
      this.list = JSON.parse(this.$route.query.data);
      this.obstacleArr = this.list.obstacle;
    },
    //
    getNursingassessment() {
      var postData = {
        key: "u_snkey",
        value: this.list.snkey,
      };

      //
      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/nursingassessment",
          qs.stringify(postData)
        )
        .then((rs) => {
          // console.log(rs.data)
          if (rs.data.length > 0) {
            this.NursingassessmentItems = JSON.parse(rs.data[0].datalist);
          }

          // this.$store.commit("setLoadingOff"); //off
        });
    },
  },
};
</script>

